/* Colors */
#contact_modal {
  display: none;
}

.photographer_header {
  display: flex;
  background-color: #FAFAFA;
  justify-content: space-around;
  align-items: center;
  height: 300px;
  margin: 0 100px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-columns: 50px, auto;
  justify-content: space-between;
  padding: 100px;
}

.media {
  height: 300px;
  object-fit: cover;
}
.media_card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 350px;
}
.media_description {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.media_title, .media_likes {
  color: #D3573C;
  font-size: 24px;
}

.contact_me {
  font-size: 20px;
  font-weight: bold;
  color: white;
  padding: 11px;
  width: 170px;
  height: 70px;
  border: none;
  background-color: #901C1C;
  border-radius: 5px;
  cursor: pointer;
}

.modal {
  border-radius: 5px;
  background-color: #DB8876;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 35px;
  margin: auto;
}
.modal header {
  justify-content: space-between;
  width: 100%;
}
.modal header h2 {
  font-size: 64px;
  font-weight: normal;
  text-align: center;
}
.modal img {
  cursor: pointer;
}

form {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
}
form label {
  color: #312E2E;
  font-size: 36px;
}
form div {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: self-start;
  margin-bottom: 26px;
}
form input {
  width: 100%;
  height: 68px;
  border: none;
  border-radius: 5px;
}